import {mapState} from 'vuex'
import {deleteArticles} from '@/api/article'
export default {
  name: 'ArticleMeta',
  props: {
    article: {
      type: Object,
      required: true
    }
  },
  computed:{
    ...mapState(['user'])
  },
  methods:{
    toDeleteArticle(slug){
      deleteArticles(slug).then(res=>{
        this.$router.push('/')
      })
    }
  }
}
